<template >
   <div class="bg-blue_1 p-10 grid gap-5">

      <!-- Form Solicitud -->
      <div class="flex flex-col p-10 border gap-5">

         <div class="grid grid-cols-1 md:grid-cols-2 bg-blue_1 gap-5">
            <!-- Lugar -->
            <div class="flex flex-col">                              
               <label class="text-white">Lugar</label>
               <Multiselect
                  mode="single"
                  v-model="form.car_pickup_place"
                  :searchable="true"
                  :options="car_pickup_places"
                  :classes="select_style_opt"
                  placeholder="Seleccione Lugar *"
               />
            </div>
   
            <!-- Tiempo -->
            <div class="">
               <label class="text-white">Rango de fecha a reservar</label>                              
               <div >
                  <Datepicker 
                     class="datepicker-container"
                     placeholder="Fecha de entrada y salida *"
                     v-model="form.dates"
                     :format="'dd/MM/yyyy'"
                     range multi-calendars 
                     auto-apply
                     :enable-time-picker="false"
                     :close-on-auto-apply="true"
                     @update:modelValue="handleDate"
                  />                              
               </div>
            </div>
   
            <!-- Horario Recogida -->
            <div class="flex flex-col">
               <label class="text-white">Horario Recogida</label>                           
               <Multiselect
                  mode="single"
                  v-model="form.timePickup"
                  :searchable="true"
                  :options="horario"
                  :classes="select_style_opt"
                  placeholder="Seleccione Horario"
               />
            </div>
   
            <!-- Horario Entrega -->
            <div class="flex flex-col">
               <label class="text-white">Horario Entrega</label>                     
               <Multiselect
                  mode="single"
                  v-model="form.timeDropOff"
                  :searchable="true"
                  :options="horario"
                  :classes="select_style_opt"
                  placeholder="Seleccione Horario"
               />
            </div>
            
   
         </div>
      
         <!-- Btn Solicitar -->
         <div class="flex flex-row-reverse items-center">
            <button type="submit" class="px-4 py-1 text-xl text-white uppercase border bg-orange_1 border-orange_1 hover:bg-transparent hover:text-orange_1">
               <i class="fa fa-spinner fa-pulse" ></i>
               Buscar
            </button>                  
         </div>
      </div>
      
   


      <div class="flex flex-1 flex-row border border-white p-10">
         <!-- <h2 class="text-white">{{ car.alias }}</h2>         
         <h2 class="text-white">{{ car.price_from }}</h2>         
         <h2 class="text-white">{{ car.num_pax }}</h2>         
         <h2 class="text-white">{{ car.tanque }}</h2>         
         
         <h2 class="text-white">{{ car_category }}</h2>
         <h2 class="text-white">{{ car_category.alias }}</h2>
         <h2 class="text-white">{{ car_category.description }}</h2>
         
         <h2 class="text-white">{{ car_engine }}</h2>
         <h2 class="text-white">{{ car_engine.alias }}</h2>
         
         <h2 class="text-white">{{ car_company }}</h2>
         <h2 class="text-white">{{ car_company.alias }}</h2>
         <h2 class="text-white">{{ car_company.phone }}</h2>

         <h2 class="text-white">{{ car_pickup_places }}</h2>
         <h2 class="text-white">{{ car_pickup_places.alias }}</h2>
         <h2 class="text-white">{{ car_pickup_places.pickup_time }}</h2>
         <h2 class="text-white">{{ car_pickup_places.dropoff_time }}</h2>
         <h2 class="text-white">{{ car_pickup_places.address }}</h2> -->
      </div>
   </div>
</template>
<script>

import Multiselect from '@vueform/multiselect'
import '@vueform/multiselect/themes/default.css'
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'


import CarService from '@/services/car.service'

export default {
   name: 'CarDetail',   
   components: {    
      Multiselect,
      Datepicker,
   },
   props: {
    keyword: String
   },  
   data() {
      return {
         car: Object,
         car_pickup_places: [],
         horario: [],
         form:{
            activeTerm: false,
            name: '',
            email: '',
            phone: '',
            passport: '',
            pickupAirport: '',
            withFuel: '',
            car_pickup_place: '',
            dates: '',
            timePickupdates: '',
            timeDropOffdates: '',
         },
         select_style_opt: {
            container: 'relative flex items-center justify-end box-border cursor-pointer border text-gray-600 bg-blue_1 text-sm leading-snug outline-none',
            search: 'absolute inset-0 outline-none focus:ring-0 appearance-none box-border text-white border-8 border-transparent text-sm font-sans bg-blue_1 pl-2 rtl:pl-0 rtl:pr-3.5 text-white',                                                        
            optionSelected: 'text-white bg-light_gray_1',
            optionSelectedPointed: 'text-white bg-light_gray_1 opacity-90',
            optionSelectedDisabled: 'text-green-100 bg-light_gray_1 bg-opacity-50 cursor-not-allowed',
         },
      }
   },
   computed: {
      isActiveTerm() {
         return (this.activeTerm) ? 'text-orange_1' : 'text-white'
      }
   },
   async created() {
      
      const car = await CarService.filter({ keyword: this.keyword })
      if(car.length > 0){
         this.car = car[0]
         this.car_pickup_places = CarService.transformToList(car[0].car_company.car_pickup_places)
      }
      
      this.getHorario()


   },
   methods: {
      getHorario(){
         this.horario = [];
         for (let i = 0; i < 24; i++) {
         const hora = i < 10 ? `0${i}:00` : `${i}:00`;
         this.horario.push(`${hora} AM`);
         }

         for (let i = 0; i < 12; i++) {
         const hora = i < 10 ? `0${i}:00` : `${i}:00`;
         this.horario.push(`${hora} PM`);
         }
      }
      
   }
}
</script>
<style scoped>     
::v-deep .dp__input{
   background-color: var(--blue_1);
   border-radius: 0px;
   min-height: calc(var(--ms-border-width, 1px)*2 + var(--ms-font-size, 1rem)*var(--ms-line-height, 1.375) + var(--ms-py, .5rem)*2);
 }
 ::v-deep .datepicker-container, ::v-deep .datepicker-container input {
   color: #fff !important;
 }
  
 ::v-deep .multiselect-single-label{
   color: #fff;
 } 
</style>