<template>
   <div>
     <component :is="productComponent"  :poleKey="poleKey" :productKey="productKey" :keyword="keyword"/>
   </div>
 </template>
 
 <script>
 import CarDetail from './CarDetail.vue'
 import HotelDetail from './HotelDetail.vue'
 
 export default {
  name:'ProductDetailWrapper',
   components: {
     CarDetail,
     HotelDetail
   },
   data() {
     return {
       poleKey: this.$route.params.poleKey,
       productKey: this.$route.params.productKey,
       keyword: this.$route.params.keyword
     };
   },
   computed: {
     productComponent() {
       switch (this.productKey) {
         case 'car':
           return 'CarDetail';
         case 'hotel':
           return 'HotelDetail';
         default:
           // manejar cualquier otro caso, posiblemente mostrar un error o redirigir
           return null;
       }
     }
   }
 };
 </script>
 